import { getPhotosEndPoint, Photos } from "src/api/photosEndPoint";
import React, { useState, useEffect } from "react";

const Main: React.FC = () => {
  const [rover, setRover] = useState("Curiosity");
  const [img, setImg] = useState("");

  const randomPhotoFromPhotosArr = (photos: Photos) => {
    return photos[Math.floor(Math.random() * photos.length)];
  };
  const getRandomPhotoFromAPI = async () => {
    const apiResp = await getPhotosEndPoint(rover, 1000);
    return randomPhotoFromPhotosArr(apiResp).img_src;
  };
  const handleButton = () => {
    getRandomPhotoFromAPI().then((randomImg) => setImg(randomImg));
  };

  return (
    <div className='center'>
      <select value={rover} onChange={(event) => setRover(event.target.value)}>
        <option value='Curiosity'>Curiosity</option>
        <option value='Opportunity'>Opportunity</option>
        <option value='Spirit'>Spirit</option>
      </select>
      <br />
      <button type='button' onClick={handleButton}>
        Случайное фото
      </button>
      <br />
      <img alt='RandomRoverPhoto' src={img} />
    </div>
  );
};

export default Main;
